import React from 'react';

export default class DateCard extends React.Component {
	render() {
		const cardContainer = {
			width: "90px",
			height: "85px",
			textAlign: "center",
			display: "flex",
			flexDirection: "column",
			border: "solid",
			borderRadius: "7px"
		}
		
		const weatherIcon = {
			align: "center"
		}
		
		
		return (
			<div style={cardContainer}>
				<div>{this.props.date}</div>
				<div style={weatherIcon}>{this.props.weather}</div>
				<div>{this.props.high} {this.props.low}</div>
			</div>
		);
	}
}

